const settingsReducer = (state, action) => {

  if (!state) {
    return {
      show: false
    };
  }

  switch (action.type) {
    
    case 'SHOW_SETTINGS': {
      return Object.assign({}, state, {
        show: action.show
      });
    }
    
    default:
      return state;
  }

};

export default settingsReducer;



