/* global chrome */
function displayWebSpeedListener (request) {
  if (request.message === 'displayWebSpeed') {
    this.displayWebSpeed();
  }
}


export function bindListeners(component) {
  chrome.runtime.onMessage.addListener(displayWebSpeedListener.bind(component));
}

export function unbindListeners(component) {
  chrome.runtime.onMessage.removeListener(displayWebSpeedListener.bind(component));
  
}



