const popupReducer = (state, action) => {

  if (!state) {
    return {
      show: false,
      showShare: false,
      testRunning: false
    };
  }

  switch (action.type) {
    
    case 'SHOW_POPUP': {
      return Object.assign({}, state, {
        show: action.show
      });
    }
    
    case 'SHOW_SHARE': {
      return Object.assign({}, state, {
        showShare: action.show
      });
    }
    
    case 'TEST_RUNNING': {
      return Object.assign({}, state, {
        testRunning: action.running
      });
    }
    
    default:
      return state;
  }

};

export default popupReducer;



