export const setTiming = (timing) => {
  return {
    type: 'SET_TIMING',
    timing,
    favicon: ''
  };
};

export const setHost = (host) => {
  return {
    type: 'SET_HOST',
    host
  };
};

export const setFavicon = (favicon) => {
  return {
    type: 'SET_FAVICON',
    favicon
  };
};

export const showWebSpeed = (show) => {
  return {
    type: 'SHOW_WEBSPEED',
    show
  };
};



