const errorReducer = (state, action) => {

  if (!state) {
    return {
      message: null,
      show: false
    };
  }

  switch (action.type) {
    
    case 'SET_ERROR': {
      return Object.assign({}, state, {
        message: action.message,
        id: action.id
      });
    }
    
    case 'SHOW_ERROR': {
      return Object.assign({}, state, {
        show: action.show
      });
    }
    
    default:
      return state;
  }

};

export default errorReducer;



