/* globals window chrome */

import React from 'react';
import * as ReactRedux from 'react-redux';
import overallActions from './overall_actions';

// extension components
import Icons from './icons/icons';
import PopupComponent from './popup/popup';
import * as PopupListeners from './popup/listeners';
import SettingsComponent from './settings/settings';
import LoginComponent from './login/login';
import OptionsComponent from './options/options';
import gtm from 'gtm';
import user from 'storage/user';

class ExtensionComponent extends React.Component {

  constructor(props) {
    super(props);
  }

  componentDidMount() {
    gtm.instantiate();

    PopupListeners.bindListeners(this);


    // if navigating from the chrome ext options menu.
    // simple solution to avoid any routing dependencies for 1 endpoint.
    if (window.location.search === '?options=true') {
      this.props.actions.showOptions(true);
    } else {
      this.props.actions.showPopup(true);
    }

    // check if the user is logged in.
    user.isLoggedIn((data) => {
      if (data.loggedIn) {
        this.props.actions.setLoggedIn(data.loggedIn, data.user.userName);
      }

      chrome.storage.local.get('options', (res) => {
        if (res.options) {
          var status = res.options.webspeed ? 'enabled' : 'disabled';
          gtm.push({'webspeed': status});
        } else {
          gtm.push({'webspeed': 'disabled'});

        }


        const manifest = chrome.runtime.getManifest();
        gtm.push({'extensionVersion': manifest.version, 'event': 'open'});
      });

    });




  }

  componentWillUnmount() {
    PopupListeners.unbindListeners(this);
  }

  renderPopup () {
    if (!this.props.popup.show) {
      return null;
    }
    return <PopupComponent
      actions = {this.props.actions}
      webspeed = {this.props.webspeed}
      showShare = {this.props.popup.showShare}
      testRunning = {this.props.popup.testRunning}
    />;
  }

  renderSettings () {
    if (!this.props.settings.show) {
      return null;
    }
    return <SettingsComponent
      loggedIn = {this.props.login.status}
      username = {this.props.login.username}
      actions = {this.props.actions}
    />;
  }

  renderLogin () {
    if (!this.props.login.show) {
      return null;
    }
    return <LoginComponent
      loggedIn = {this.props.login.status}
      actions = {this.props.actions}
      error = {this.props.error}
    />;
  }

  renderOptions () {
    if (!this.props.options.show) {
      return null;
    }
    return <OptionsComponent
      actions = {this.props.actions}
    />;
  }

  render() {
    return <div>
    <Icons />
      {this.renderPopup()}
      {this.renderSettings()}
      {this.renderLogin()}
      {this.renderOptions()}
    </div>;
  }
}

ExtensionComponent.defaultProps = {};

const mapDispatchToActions = overallActions;
const mapStateToProps = (allState) => allState;
const extcontainer = ReactRedux.connect(
  mapStateToProps,
  mapDispatchToActions
)(ExtensionComponent);

export default extcontainer;



