/* global document */

import React from 'react';
import user from 'storage/user';
import helpers from 'client/helpers';
import {FormattedMessage} from 'react-intl';
import gtm from 'gtm';


class SettingsComponent extends React.Component {

  constructor(props) {
    super(props);
  }

  showLogin() {
    this.props.actions.showSettings(false);
    this.props.actions.showLogin(true);
  }

  showPopup() {
    this.props.actions.showSettings(false);
    this.props.actions.showPopup(true);
  }

  showOptions() {
    this.props.actions.showSettings(false);
    this.props.actions.showOptions(true);
  }

  signOut() {
    user.logOut(() => {
      this.props.actions.setLoggedIn(false);
    });
  }

  componentDidMount() {
    helpers.setWebspeedOption();
    document.body.className = 'settings';

    gtm.push({'page': 'settings', 'event': 'pageView'});
  }

  componentWillUnmount() {
    document.body.className = '';
  }

  render() {
    const signedInClass = this.props.loggedIn ? 'settings-signed-out' : 'settings-signed-in';
    const signedOutClass = this.props.loggedIn ? 'settings-signed-in' : 'settings-signed-out';
    const username = this.props.loggedIn ?
      this.props.username :
      <FormattedMessage id="not_logged_in" defaultMessage="NOT LOGGED IN" />;

    return <div className="wrapper settings-wrapper">
        <div className='header-assembly settings-header pure-g'>
          <div className="header-left pure-u-1-6">
            <a href="#" onClick={this.showPopup.bind(this)}>
              <svg className='svg-icon'>
                <use xlinkHref='#speedtest-chrome-extension-icon-back' />
              </svg>
            </a>
          </div>
          <div className='settings-user pure-u-2-3'>
            <svg className='svg-icon'>
              <use xlinkHref='#speedtest-chrome-extension-icon-account' />
            </svg>
            <span className='settings-username'>{username}</span>
          </div>
        </div>
        <div className={`settings-upper ${signedOutClass}`}>
            <p>
              <FormattedMessage id="signin_message"
                defaultMessage="Sign in to save your results and view your result history" />
            </p>
            <a
              className="pure-button pure-button-primary pure-button-wide"
              href="#"
              onClick={this.showLogin.bind(this)}
            >
              <FormattedMessage id="signin" defaultMessage="Sign In" />
            </a>
        </div>


        <ul className='settings-list'>
          <li className='heavy upper'>
            <FormattedMessage id="web_speed" defaultMessage="Web Speed" />
            <a onClick={this.showOptions.bind(this)} href="#">
              <svg className='svg-icon svg-question'>
                <use xlinkHref='#speedtest-chrome-extension-icon-question' />
              </svg>
            </a>
            <input type="checkbox" id="webspeed" />
            <label htmlFor="webspeed" onClick={helpers.saveWebspeed.bind(this)}></label>
          </li>
          <li className={`heavy list-link ${signedInClass}`}>
            <a
              onClick={helpers.externalLink.bind(this, 'http://www.speedtest.net/results')}
              href='#'
            >
              <FormattedMessage id="result_history" defaultMessage="RESULT HISTORY" />
            </a>
          </li>
          <li className={`heavy list-link ${signedInClass}`}>
            <a
              onClick={helpers.externalLink.bind(this, 'http://www.speedtest.net/settings')}
              href='#'
            >
              <FormattedMessage id="account_settings" defaultMessage="ACCOUNT SETTINGS" />
            </a>
          </li>
          <li className='list-link'>
            <a
              onClick={helpers.externalLink.bind(this, 'http://www.speedtest.net/about')}
              href='#'
            >
              <FormattedMessage id="about" defaultMessage="About" />
            </a>
          </li>
          <li className='list-link'>
            <a
              onClick={helpers.externalLink.bind(this, 'http://www.speedtest.net/privacy')}
              href='#'
            >
              <FormattedMessage id="privacy" defaultMessage="Privacy Policy" />
            </a>
          </li>
          <li className='list-link'>
            <a
              onClick={helpers.externalLink.bind(this, 'http://www.speedtest.net/terms')}
              href='#'
            >
              <FormattedMessage id="terms" defaultMessage="Terms of Use" />
            </a>
          </li>
          <li className={`list-link ${signedInClass}`}>
            <a className='logout' href="#" onClick={this.signOut.bind(this)}>
              <FormattedMessage id="signout" defaultMessage="Sign Out" />
            </a>
          </li>
        </ul>
      </div>;
  }
}

SettingsComponent.defaultProps = {};

export default SettingsComponent;



