export const showPopup = (show) => {
  return {
    type: 'SHOW_POPUP',
    show
  };
};

export const showShare = (show) => {
  return {
    type: 'SHOW_SHARE',
    show
  };
};

export const testRunning = (running) => {
  return {
    type: 'TEST_RUNNING',
    running
  };
};



