/* global chrome */
import gtm from 'gtm';

function runningStartListener (request) {
  if (request.message === 'startTest' || request.message === 'testRunning') {
    if (!this.props.popup.testRunning) {
      this.props.actions.testRunning(true);
    }
    if (this.props.popup.showShare) {
      this.props.actions.showShare(false);
    }
  }
}

function setFinishedListener (request) {
  if (request.message === 'setFinished') {
    if (this.props.testRunning) {
      this.props.actions.testRunning(false);
    }
  }
}

function resultSavedListener (request) {
  if (request.message === 'resultSaved') {
    if (!this.props.testRunning) {
      this.props.actions.showShare(true);
    }
  }
}

function backgroundLoadedListener(request) {
  if (request.message === 'backgroundLoaded') {
    console.error('chrome background reloaded');
    gtm.push({'event': 'backgroundReloaded'});
  }
}

export function bindListeners(component) {
  chrome.runtime.onMessage.addListener(runningStartListener.bind(component));
  chrome.runtime.onMessage.addListener(setFinishedListener.bind(component));
  chrome.runtime.onMessage.addListener(resultSavedListener.bind(component));
  chrome.runtime.onMessage.addListener(backgroundLoadedListener.bind(component));
}

export function unbindListeners(component) {
  chrome.runtime.onMessage.removeListener(resultSavedListener.bind(component));
  chrome.runtime.onMessage.removeListener(setFinishedListener.bind(component));
  chrome.runtime.onMessage.removeListener(runningStartListener.bind(component));
  chrome.runtime.onMessage.removeListener(backgroundLoadedListener.bind(component));
}



