/* global chrome document */
import React from 'react';
import {FormattedNumber} from 'react-intl';
import {FormattedMessage} from 'react-intl';
import helpers from 'client/helpers';
import * as WebSpeedListeners from './listeners';

class WebspeedComponent extends React.Component {

  constructor(props) {
    super(props);
  }

  refreshTab () {
    chrome.tabs.query({active: true, currentWindow: true}, (arrayOfTabs) => {
      var code = 'window.location.reload();';
      chrome.tabs.executeScript(arrayOfTabs[0].id, {code: code});
    });
  }

  urlDomain(data) {
    const a = document.createElement('a');
    a.href = data.url;
    const url = helpers.parseUrl(a);
    const tab = {
      host: url.host,
      pathname: url.pathname,
      favicon: data.favIconUrl || ''
    };

    return tab;
  }

  displayWebSpeed () {
    chrome.storage.local.get('webspeed', (data) => {
      chrome.tabs.query({currentWindow: true, active: true}, (tabs) => {
        const tab = this.urlDomain(tabs[0]);

        if (data.webspeed &&
          data.webspeed[tab.host] &&
          data.webspeed[tab.host][tab.pathname] &&
          data.webspeed[tab.host][tab.pathname].domCompleteDuration) {

          this.props.actions.setFavicon(tab.favicon);
          this.props.actions.setHost(tab.host);
          this.props.actions.setTiming(data.webspeed[tab.host][tab.pathname].domCompleteDuration);
          this.props.actions.showWebSpeed(true);

        } else {

          chrome.storage.local.get('options', (res) => {
            if (Object.keys(res).length === 0 ) {
              res = {};
              res.options = {};
            }
            if (res.options.webspeed) {
              // don't show webspeed information for chrome tabs, or webstore
              var url = tabs[0].url;
              if (url.indexOf('chrome://') < 0 &&
                url.indexOf('https://chrome.google.com/webstore') < 0) {
                this.props.actions.setFavicon(tab.favicon);
                this.props.actions.setHost(tab.host);
                this.props.actions.setTiming('reload');
                this.props.actions.showWebSpeed(true);
              } else {
                this.props.actions.showWebSpeed(false);
              }
            } else {
              this.props.actions.showWebSpeed(false);
            }
          });

        }
      });
    });
  }


  componentDidMount () {
    this.displayWebSpeed();
    WebSpeedListeners.bindListeners(this);
  }

  componentWillUnmount () {
    WebSpeedListeners.unbindListeners(this);
  }

  render() {

    const favStyle = {
      backgroundImage: `url(${this.props.webspeed.favicon})`,
      backgroundSize: '16px 16px'
    };

    let timing;
    let favicon;
    if (this.props.webspeed.favicon) {
      favicon = <div className="favicon-background" style={favStyle}></div>;
    }

    if (this.props.webspeed.timing === 'reload') {
      timing = <a href="#" onClick={this.refreshTab.bind(this)} className="refresh-tab">
        <svg className="svg-icon">
          <use xlinkHref="#speedtest-chrome-extension-icon-reload" />
        </svg>
      </a>;
    } else if (this.props.webspeed.timing !== null) {
      timing = <span><FormattedNumber value={this.props.webspeed.timing} /> sec</span>;
    } else {
      timing = '';
      favicon = '';
    }

    let webspeed;

    if (this.props.webspeed.show) {
      webspeed = <div className='web-speed'>
        <h3><FormattedMessage id="web_speed" defaultMessage="Web Speed" /></h3>
        <table>
          <tbody>
            <tr>
              <td className="favicon">{favicon}</td>
              <td className="host"><div className="hostname">{this.props.webspeed.host}</div></td>
              <td className="timing">{timing}</td>
            </tr>
          </tbody>
        </table>
      </div>;
    }

    return <div>{webspeed}</div>;
  }
}

WebspeedComponent.defaultProps = {};

export default WebspeedComponent;



