/* globals document, navigator */

import React from 'react';
import * as ReactDOM from 'react-dom';
import * as Redux from 'redux';
import * as ReactRedux from 'react-redux'; //eslint-disable-line

import createLogger from 'redux-logger';
import overall_reducers from './client/overall_reducers';

import { createDevTools } from 'redux-devtools';
import LogMonitor from 'redux-devtools-log-monitor' ; //eslint-disable-line
import DockMonitor from 'redux-devtools-dock-monitor'; //eslint-disable-line
import config from 'config';
import 'styles/main.less';
import localeStrings from 'locales/default';

const Tools = createDevTools(
  <DockMonitor toggleVisibilityKey='ctrl-h'
               changePositionKey='ctrl-q'>
    <LogMonitor />
  </DockMonitor>
);

const middlewares = [];

if (config.debug.logger) {
  const logger = createLogger({collapsed: true});
  middlewares.push(logger);
}

const middleware = Redux.applyMiddleware(...middlewares);
const composed = Redux.compose(middleware, Tools.instrument());

const store = composed(Redux.createStore)(overall_reducers);

const reactTools = config.debug.reactTools ? <Tools store={store} /> : '';

import ExtensionContainer from './client/extension';
import {IntlProvider, addLocaleData} from 'react-intl';

function get_language(locale) {
  // chinese variations
  const simplified = ['zh-CN', 'zh-SG'];
  const traditional = ['zh-TW', 'zh-HK', 'zh-MO'];
  let type = '';
  if (simplified.indexOf(locale) > -1) {
    // key for simplified chinese translation
    type = 's';
  }

  if (traditional.indexOf(locale) > -1) {
    // key for traditional chinese translation
    type = 't';
  }
  const dash_index = locale.indexOf('-');
  if (dash_index >= 0) {
    return {language: locale.substring(0, dash_index), type: type};
  }
  return {language: locale, type: type};
}

// determine language load localized data.
const l = get_language(navigator.language || navigator.browserLanguage);

const locale = require('react-intl/locale-data/' + l.language);
addLocaleData([
  ...locale
]);

const localizedStrings = localeStrings[l.language + l.type] || localeStrings.en;

const mainComponent = (
  <div>
    <ReactRedux.Provider store={store}>
      <IntlProvider locale={l.language} messages={localizedStrings}>
        <ExtensionContainer />
      </IntlProvider>
    </ReactRedux.Provider>
    {reactTools}
  </div>
);

const mount = document.getElementById('extension');
ReactDOM.render(mainComponent, mount);



