const optionsReducer = (state, action) => {

  if (!state) {
    return {
      show: false
    };
  }

  switch (action.type) {
    
    case 'SHOW_OPTIONS': {
      return Object.assign({}, state, {
        show: action.show
      });
    }
    
    default:
      return state;
  }

};

export default optionsReducer;



