const webSpeedReducer = (state, action) => {

  if (!state) {
    return {
      timing: null,
      host: null,
      show: false
    };
  }

  switch (action.type) {

    case 'SET_TIMING': {
      return Object.assign({}, state, {
        timing: action.timing
      });
    }

    case 'SET_HOST': {
      return Object.assign({}, state, {
        host: action.host
      });
    }

    case 'SET_FAVICON': {
      return Object.assign({}, state, {
        favicon: action.favicon
      });
    }

    case 'SHOW_WEBSPEED': {
      return Object.assign({}, state, {
        show: action.show
      });
    }

    default:
      return state;
  }

};

export default webSpeedReducer;



