import React from 'react';
import helpers from 'client/helpers';
import {FormattedMessage} from 'react-intl';

class ErrorComponent extends React.Component {

  constructor(props) {
    super(props);
  }

  render() {

    const err = this.props.error.id;

    let message = 'Login error.';
    if (err === 'login_error_generic') {
      message = 'Invalid email or password.';
    } else if (err === 'login_error_unconfirmed') {
      message = 'Unconfirmed email address.';
    } else if (err === 'login_error_wrong_account_type') {
      message = 'Incorrect account type.';
    }

    let errorMessage;
    if (err) {
      errorMessage = <FormattedMessage id={err} defaultMessage={message} />;
    } else {
      errorMessage = message;
    }

    let errorHelp;
    if (err === 'login_error_generic') {
      const errorHelpMessage = <FormattedMessage id='forgot_password' defaultMessage='Forgot your Password?' />;
      const errorLink = 'http://www.speedtest.net/request-reset';
      errorHelp = <p><a onClick={helpers.externalLink.bind(this, errorLink)} href={errorLink}>{errorHelpMessage}</a></p>;
    }

    return <div>
      <div className='error-container'>
        <p>{errorMessage}</p>
        {errorHelp}
      </div>
    </div>;
  }
}

ErrorComponent.defaultProps = {};

export default ErrorComponent;



