import * as Redux from 'redux';
import * as popup from './popup/actions';
import * as settings from './settings/actions';
import * as login from './login/actions';
import * as options from './options/actions';
import * as webspeed from './webspeed/actions';
import * as error from './error/actions';

const dispatch = function mapDispatchToProps(dispatch) {
  const result = {
    actions: Redux.bindActionCreators(
      Object.assign({},
      popup,
      settings,
      login,
      options,
      webspeed,
      error
    ), dispatch)
  };

  return result;
};

export default dispatch;



