/* global chrome, window */
import React from 'react';
import gaugeBridge from 'scripts/popupGaugeBridge';
import WebSpeedComponent from 'client/webspeed/webspeed';
import gtm from 'gtm';
class PopupComponent extends React.Component {

  constructor(props) {
    super(props);
  }

  showSettings() {
    this.props.actions.showPopup(false);
    this.props.actions.showSettings(true);
  }

  showShare() {
    chrome.runtime.sendMessage({message: 'shareResult', share: true});
    gtm.push({'event': 'shareButtonClick'});
  }

  resetTest() {
    chrome.runtime.sendMessage({message: 'resetTest'});
  }

  renderWebSpeed () {
    return <WebSpeedComponent
      webspeed = {this.props.webspeed}
      actions = {this.props.actions}
    />;
  }

  componentDidMount() {
    gaugeBridge.create();
    gtm.push({'page': 'popup', 'event': 'pageView'});
  }

  render() {
    let share;
    if (this.props.showShare) {
      share = <a onClick={this.showShare.bind(this)} className='share' href="#">
        <svg className='svg-icon'>
          <use xlinkHref='#speedtest-chrome-extension-icon-share' />
        </svg>
      </a>;
    }

    return <div className='wrapper'>
      <div className='header-assembly pure-g'>
        <div className="header-left pure-u-1-4">
          <a onClick={this.resetTest.bind(this)} className='resetTest' href="#">
            <svg className='svg-icon'>
              <use xlinkHref='#speedtest-chrome-extension-icon-back' />
            </svg>
          </a>
        </div>
        <div className='header-logo pure-u-1-2'>
          <svg className='svg-logo'><use xlinkHref='#speedtest-chrome-extension-logo' /></svg>
        </div>
        <div className='header-right pure-u-1-4'>
          {share}
          <a onClick={this.showSettings.bind(this)} className='account-settings' href="#">
            <svg className='svg-icon'>
              <use xlinkHref='#speedtest-chrome-extension-icon-account' />
            </svg>
          </a>
        </div>
      </div>
      <div id='speedtest-container'></div>
      {this.renderWebSpeed()}

    </div>;
  }
}

PopupComponent.defaultProps = {};

export default PopupComponent;



