import React from 'react';
import user from 'storage/user';
import helpers from 'client/helpers';
import ErrorComponent from 'client/error/error';
import gtm from 'gtm';

import {FormattedMessage, FormatMessage, injectIntl} from 'react-intl';

class LoginComponent extends React.Component {

  constructor(props) {
    super(props);
  }

  logIn(e) {
    e.preventDefault();
    user.login((response) => {
      if (response.status === 'error') {
        this.props.actions.showError(false);
        this.props.actions.setError(response.message, response.id);
        this.props.actions.showError(true);
      } else {
        this.props.actions.showError(false);
        this.props.actions.setLoggedIn(true, response);
        this.props.actions.showLogin(false);
        this.props.actions.showPopup(true);
      }

    });
  }

  showSettings() {
    this.props.actions.showLogin(false);
    this.props.actions.showSettings(true);
  }

  showPopup() {
    this.props.actions.showLogin(false);
    this.props.actions.showPopup(true);
  }

  componentDidMount() {
    this.props.actions.showError(false);
    gtm.push({'page': 'login', 'event': 'pageView'});
  }

  render() {
    let error;
    if (this.props.error.show) {
      error = <ErrorComponent error={this.props.error} />;
    }

    return <div className="wrapper">
      <div className='header-assembly pure-g'>
        <div className="header-left pure-u-1-4">
          <a href="#" onClick={this.showPopup.bind(this)}>
            <svg className='svg-icon'><use xlinkHref='#speedtest-chrome-extension-icon-back' /></svg>
          </a>
        </div>
        <div className='header-logo pure-u-1-2'>
          <span className='pages-header'>
            <FormattedMessage id="signin" defaultMessage="Sign In" />
          </span>
        </div>
        <div className='header-right pure-u-1-4'>
          <a href="#" onClick={this.showSettings.bind(this)}>
            <svg className='svg-icon'><use xlinkHref='#speedtest-chrome-extension-icon-account' /></svg>
          </a>
        </div>
      </div>

      {error}

      <form id="login" className="pure-form pure-form-stacked login-form" onSubmit={this.logIn.bind(this)}>
        <fieldset>
          <input type="email" className="full-width" placeholder={this.props.intl.formatMessage({id: 'email'})} name="email" />
          <input type="password" className="full-width" placeholder={this.props.intl.formatMessage({id: 'password'})} name="password" />
          <div className='lower-sign-in'>
            <button type="submit" className="pure-button pure-button-primary pure-button-wide"><FormattedMessage id="signin" defaultMessage="Sign In" /></button>
            <p><FormattedMessage id="first_time_here" defaultMessage="First time here?" /> <a onClick={helpers.externalLink.bind(this, 'http://www.speedtest.net/register')} href="http://www.speedtest.net/register"><FormattedMessage id="signup" defaultMessage="Sign Up" /></a></p>
          </div>
        </fieldset>
      </form>
    </div>;
  }
}

LoginComponent.defaultProps = {};

export default injectIntl(LoginComponent);



