/* global chrome */
const $ = require('jquery');
const config = require('config');
import gtm from 'gtm';

module.exports = {
  save: function (data, next) {
    chrome.storage.local.get('user', (user) => {
      if (Object.keys(user).length === 0 ) {
        user = {};
      }
      user = {
        username: data.user.userName,
        loggedIn: data.loggedIn
      };
      chrome.storage.local.set({'user': user}, function () {
        next();
      });
    });
  },

  get: function (next) {
    chrome.storage.local.get('user', (user) => {
      next(user);
    });
  },

  remove: function() {
    chrome.storage.local.remove('user');
  },

  isLoggedIn: function (next) {
    $.ajax({
      type: 'GET',
      url: config.api.globals.path,
      success: (data) => {
        if (data.loggedIn) {
          gtm.push({'accountStatus': 'loggedIn', 'accountId': data.user.hash});
          this.save(data, function () {
            next(data);
          });
        } else {
          gtm.push({'accountStatus': 'anonymous'});
          this.remove();
          next(data);
        }
      },
      error: () => {
        gtm.push({'accountStatus': 'anonymous'});
      }
    });
  },

  logOut: function (next) {
    $.ajax({
      type: 'POST',
      url: config.api.userLogout.path,
      success: (data) => {
        // remove user information from local storage.
        this.remove();
        if (data.status) {
          gtm.push({'accountStatus': 'anonymous', 'event': 'loggedOut'});
          next();
        }
      },
      error: function () {
        $('#error').text('Logout unsuccessful.');
      }
    });
  },

  login: function (next) {
    $.ajax({
      type: 'POST',
      url: config.api.userLogin.path,
      data: $('#login').serialize(),
      success: function (data) {
        if (data.status) {

          gtm.push({
            'accountStatus': 'loggedIn',
            'event': 'loggedIn',
            'accountId': data.user.hash
          });
          next(data.user.userName);
        }
      },
      error: function (xhr, status, error) {
        var err = 'login_error';
        if (xhr.responseJSON && xhr.responseJSON.errors) {
          for (error in xhr.responseJSON.errors) {
            err = xhr.responseJSON.errors[error];
          }
        }
        gtm.push({'event': 'invalidCredentials'});
        next({
          status: 'error',
          id: err
        });
      }
    });
  }
};


