/* global window */
import React from 'react';

import helpers from 'client/helpers';
import {FormattedMessage} from 'react-intl';
import gtm from 'gtm';

class OptionsComponent extends React.Component {

  constructor(props) {
    super(props);
  }
  
  
  showSettings() {
    this.props.actions.showOptions(false);
    this.props.actions.showSettings(true);
  }
  
  
  componentDidMount() {
    helpers.setWebspeedOption();
    gtm.push({'page': 'options', 'event': 'pageView'});
  }

  render() {
    let backButton;
    let settingsButton;
    // don't show the back button when navigating from the actual options menu.
    if (window.location.search !== '?options=true') {
      backButton = <a onClick={this.showSettings.bind(this)} href="#" className='backButton'>
        <svg className='svg-icon'>
          <use xlinkHref='#speedtest-chrome-extension-icon-back' />
        </svg>
      </a>;
    }
    
    if (window.location.search !== '?options=true') {
      settingsButton = <a href="#" onClick={this.showSettings.bind(this)} className='settings-link'>
        <svg className='svg-icon'>
          <use xlinkHref='#speedtest-chrome-extension-icon-account' />
        </svg>
      </a>;
    }
    
    return <div className="wrapper">

      <div className='header-assembly pure-g'>
        <div className="header-left pure-u-1-4">
          {backButton}
        </div>
        <div className='header-logo pure-u-1-2'>
            <span className='pages-header'>OPTIONS</span>
        </div>
        <div className='header-right pure-u-1-4'>
          {settingsButton}
        </div>
      </div>

      <div className='webspeed-config'>
          <input type="checkbox" id="webspeed" />
          <label htmlFor="webspeed" onClick={helpers.saveWebspeed.bind(this)}>
          <FormattedMessage id="enable_web_speed" defaultMessage="Enable Web Speed" />
          </label>
          <p>
            <FormattedMessage
              id="web_speed_description"
              defaultMessage="Web Speed measures how fast websites load. You can check a sites Web Speed by clicking the Speedtest extension icon after the site loads. At the bottom of the extension window, youll see the time it took (in seconds)."
            />
            
            <br/>
            <a
              onClick={helpers.externalLink.bind(this, 'http://www.speedtest.net/webspeed' )}
              href="#"
            >
              <FormattedMessage id="learn_more" defaultMessage="Learn more" />
            </a>
          </p>
      </div>
      <br/>
    </div>;
  }
}

OptionsComponent.defaultProps = {};

export default OptionsComponent;



