const loginReducer = (state, action) => {

  if (!state) {
    return {
      status: false,
      username: null,
      show: false
    };
  }
  
  switch (action.type) {
    case 'SET_LOGGED_IN': {
      return Object.assign({}, state, {
        status: action.status,
        username: action.username
      });

    }
    
    case 'SHOW_LOGIN': {
      return Object.assign({}, state, {
        show: action.show
      });
    }
    
    default:
      return state;
  }
};

export default loginReducer;



