/* global chrome, window */

const $ = require('jquery');
// Init Gauge
const engine = {

  listeners: {},

  initialize: function (next) {
    chrome.runtime.sendMessage({message: 'initialize'});
    next();
  },

  startTest: function (server, next) {
    chrome.runtime.sendMessage({message: 'startTest', server: server});
    this.resetHeader();
    next();
  },

  engineAvailable: function (next) {
    if (!this.listeners.engineAvailable) {
      this.listeners.engineAvailable = (request) => {
        if (request.message === 'engineAvailable') {
          next();
        }
      };
    }
    chrome.runtime.onMessage.removeListener(this.listeners.engineAvailable);
    chrome.runtime.onMessage.addListener(this.listeners.engineAvailable);
  },

  serversSorted: function (next) {
    if (!this.listeners.serversSorted) {
      this.listeners.serversSorted = (request) => {
        if (request.message === 'serversSorted') {
          chrome.storage.local.get('sortedServers', function (servers) {
            next(servers);
          });
        }
      };
    }
    chrome.runtime.onMessage.removeListener(this.listeners.serversSorted);
    chrome.runtime.onMessage.addListener(this.listeners.serversSorted);
  },

  updateProgress: function (next) {
    if (!this.listeners.updateProgress) {
      this.listeners.updateProgress = (request) => {
        if (request.message === 'updateProgress') {
          next(request.test, request.result, request.percentage);
        }
      };
    }
    chrome.runtime.onMessage.removeListener(this.listeners.updateProgress);
    chrome.runtime.onMessage.addListener(this.listeners.updateProgress);
  },

  updateLatencyResult: function (next) {
    if (!this.listeners.updateLatencyResult) {
      this.listeners.updateLatencyResult = (request) => {
        if (request.message === 'latencyResult') {
          next(request.result);
        }
      };
    }
    chrome.runtime.onMessage.removeListener(this.listeners.updateLatencyResult);
    chrome.runtime.onMessage.addListener(this.listeners.updateLatencyResult);
  },

  updateDownloadResult: function (next) {
    if (!this.listeners.updateDownloadResult) {
      this.listeners.updateDownloadResult = (request) => {
        if (request.message === 'downloadResult') {
          next(request.result);
        }
      };
    }

    chrome.runtime.onMessage.removeListener(this.listeners.updateDownloadResult);
    chrome.runtime.onMessage.addListener(this.listeners.updateDownloadResult);
  },

  updateUploadResult: function (next) {
    if (!this.listeners.updateUploadResult) {
      this.listeners.updateUploadResult = (request) => {
        if (request.message === 'uploadResult') {
          next(request.result);
        }
      };
    }
    chrome.runtime.onMessage.removeListener(this.listeners.updateUploadResult);
    chrome.runtime.onMessage.addListener(this.listeners.updateUploadResult);
  },

  showPreviousResults: function (next) {
    if (!this.listeners.showPreviousResults) {
      this.listeners.showPreviousResults = (request) => {
        if (request.message === 'showPreviousResults') {
          next(request.latency, request.download, request.upload);
        }
      };
    }
    chrome.runtime.onMessage.removeListener(this.listeners.showPreviousResults);
    chrome.runtime.onMessage.addListener(this.listeners.showPreviousResults);
  },

  testRunning: function (next) {
    if (!this.listeners.testRunning) {
      this.listeners.testRunning = (request) => {
        if (request.message === 'testStarted' && request.status) {
          // show reset button.
          $('.resetTest').show();
          chrome.storage.local.get(
            ['latencyResult',
            'downloadResult',
            'uploadResult'], (results) => {
            next(
              results.latencyResult,
              results.downloadResult,
              results.uploadResult,
              request.selectedServer
            );
          });
        }
      };
    }

    chrome.runtime.onMessage.removeListener(this.listeners.testRunning);
    chrome.runtime.onMessage.addListener(this.listeners.testRunning);
    chrome.runtime.sendMessage({message: 'isTestStarted'});
  },

  setFinished: function (next) {
    if (!this.listeners.setFinished) {
      this.listeners.setFinished = (request) => {
        if (request.message === 'setFinished') {
          $('.resetTest').hide();
          next(request.reset);
        }
      };
    }

    chrome.runtime.onMessage.removeListener(this.listeners.setFinished);
    chrome.runtime.onMessage.addListener(this.listeners.setFinished);
  },

  setTestStage: function (next) {
    if (!this.listeners.testStage) {
      this.listeners.testStage = (request) => {
        if (request.message === 'setTestStage') {
          next(request.stage);
        }
      };
    }
    chrome.runtime.onMessage.removeListener(this.listeners.testStage);
    chrome.runtime.onMessage.addListener(this.listeners.testStage);

  },

  errorMessage: function (next) {
    if (!this.listeners.errorMessage) {
      this.listeners.errorMessage = (request) => {
        if (request.message === 'errorMessage') {
          chrome.runtime.sendMessage({message: 'resetTest'});
          next(request.error);
        }
      };
    }
    chrome.runtime.onMessage.removeListener(this.listeners.errorMessage);
    chrome.runtime.onMessage.addListener(this.listeners.errorMessage);
  },

  resultSaved: function (next) {
    if (!this.listeners.resultSaved) {
      this.listeners.resultSaved = (request) => {
        if (request.message === 'resultSaved') {
          next(request.resultId);
        }
      };
    }
    chrome.runtime.onMessage.removeListener(this.listeners.resultSaved);
    chrome.runtime.onMessage.addListener(this.listeners.resultSaved);
  },

  shareResult: function (next) {
    if (!this.listeners.shareResult) {
      this.listeners.shareResult = (request) => {
        if (request.message === 'shareResult') {
          next(request.share);
        }
      };
    }
    chrome.runtime.onMessage.removeListener(this.listeners.shareResult);
    chrome.runtime.onMessage.addListener(this.listeners.shareResult);
  },
  resetHeader: function () {
    $('.resetTest').show();
  }

};

const components = {
  StartButton: true,
  Progress: true,
  Gauge: true,
  Connecting: true,
  Ping: true,
  Status: true,
  Result: true,
  ErrorMessage: true,
  Share: {
    gtm: {
      events: [
        'linkCopied',
        'facebookClick',
        'twitterClick'
      ]
    }
  },
  Background: true
};

export default {
  create: function () {
    window.createGauge(engine, components);
  }
};



